.toy-update {
    margin: auto;
    margin-top: 100px;
    max-width: 250px;
    padding: 25px;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 4px;

    h3 {
        margin-bottom: 20px;
    }

    form {
        max-width: 250px;
        min-height: 300px;
    }

    input[type="checkbox"] {
        margin-inline-end: 10px;
    }
}
